<template>
  <div class="age">
        <van-nav-bar
            title="选择年龄"
            left-text="返回"
            left-arrow
            @click-left="$router.go(-1)"
        />


      <!-- <div class="wrap" v-if="choose.id">
          <div class="tag_wrap">
                <div class="scr">
                    <div class="tag">
                        <p> {{choose.name}} </p>
                    </div>
                </div>
                <div class="clear" @click="clear">
                    <p>清除</p>
                    <van-icon name="cross"/>
                </div>
          </div>
      </div> -->


        <div class="choose_wrap">
            <div>
                <div class="item" v-for="(item, index) in list" :key="item.id" @click="chooseItem(item, index)">
                    <p :class="item.active ? 'active' : ''">{{item.name}}</p>
                </div>
            </div>
        </div>

        <div class="btn-wrap">
            <van-button type="primary" size="large" color="#2873ff" @click="submit">确定</van-button>
        </div>
  </div>
</template>
 
<script>
import { mapState, mapGetters } from "vuex";
export default {
    name: 'material',
    data () {
        return {
            choose: {
                name: '',
                id: ''
            },
            list: [
                {
                    name: '18~22',
                    id: '1',
                    active: false
                },{
                    name: '23~27',
                    id: '2',
                    active: false
                },{
                    name: '28~32',
                    id: '3',
                    active: false
                },{
                    name: '33~37',
                    id: '4',
                    active: false
                },{
                    name: '38~42',
                    id: '5',
                    active: false
                },{
                    name: '43~47',
                    id: '6',
                    active: false
                },{
                    name: '48~52',
                    id: '7',
                    active: false
                },{
                    name: '53~57',
                    id: '8',
                    active: false
                },{
                    name: '58~62',
                    id: '9',
                    active: false
                },{
                    name: '63+',
                    id: '10',
                    active: false}
            ]
        }
    },
    computed: {
        ...mapState({
            s_age: state => state.s_age
        })
    },
    created() {
        console.log( this.list )
        console.log( this.s_age )
        let arr = this.list;
        arr.forEach( (item, index) => {
            item.active = false;
            if(item.id == this.s_age.id) {
                item.active = true;
                console.log( item )
            }
        })
        this.list = arr

    },
    methods: {
        submit() {
            console.log( this.choose )
            this.$store.state.s_age = this.choose;
            this.$router.go(-1)
        },
        chooseItem(val, index) {
            this.choose = val;
            this.list.forEach( (item, _index) => {
                item.active = false;
                this.$set(this.list, _index, item);
            })
            val.active = true;
            this.$set(this.list, index, val);
            console.log( val )
        },
        clear() {
            this.choose = {
                name: '',
                id: ''
            }
        }
    }
}
</script>
 
<style scoped lang ="stylus">
.age
    .wrap
        .tag_wrap
            position relative
            display flex
            background #F8F8F8
            padding 12px
            .tag
                display flex
                align-items center
                background #FFF
                padding 4px
                border-radius 2px
            .clear
                position absolute
                right 12px
                display flex
                align-items center
                top 12px
                padding 4px
                color #2873fe
    .choose_wrap
        padding 8px
        .item 
            display inline-block
            width 25%
            box-sizing border-box
            padding 4px
            p 
                background #F8F8F8
                text-align center
                padding 12px 0
                color #333
                width 100%
            .active
                color #2873fe
                background rgba(40,115,255,0.1)
    .btn-wrap
        position fixed
        left 0
        bottom 0
        width 100%

</style>